<template>
    <div class="wrapper">
      <div class="title"> by -3-</div>
      <div class="count">{{ count }}</div>
      <div class="ope">
        <el-input v-model="input" placeholder="" :disabled="isWin" @keyup.enter.native="guess()"></el-input>
        <el-button type="primary" class="btn" :disabled="input.length !== 1" @click="guess()"></el-button>
        <el-button class="btn" @click="dialogMode = 2"></el-button>
      </div>
      <div class="content">
        <div v-for="(p, index) in ans" :key="index" class="paragraph">
          <div v-for="(c, index2) in p" :key="index2" class="block" :class="classes[c.status]">
            <span v-if="c.status !== 1">{{ c.character }}</span>
          </div>
        </div>
      </div>
      <div class="history">
        <div v-for="(h, index) in his" :key="index" class="block" :class="h.status ? 'green' : 'red'">{{ h.character }}</div>
      </div>

      <el-dialog title="" center :visible.sync="dialog1" width="300px" :show-close="false">
        <div class="out">
          <el-button type="success" :disabled="isAIThinking" @click="ai()">
            <span v-if="isAIThinking"><i class="el-icon-loading"></i>10</span>
            <span v-else>AI</span>
          </el-button>
          <el-button type="info" @click="dialogMode=2"></el-button>
        </div>
      </el-dialog>
      <el-dialog title="" center :visible.sync="dialog2" width="75%" @close="dialogMode = (isAIThinking ? 0 : 1)">
        <el-input type="textarea" :rows="10" placeholder="" v-model="textarea"></el-input>
        <span slot="footer" class="">
          <el-button type="primary" :disabled="textarea.length===0" @click="customize()"></el-button>
        </span>
      </el-dialog>
    </div>
  </template>
  
  <style scoped lang="scss">
    .wrapper{
      max-width: 800px;
      margin: 0 auto;
      padding: 0 20px;
      .title{
        margin-top: 20px;
        margin-bottom: 20px;
        font-size: 24px;
        text-align: center;
      }
      .count{
        text-align: center;
      }
      .ope{
        margin-top: 20px;
        display: flex;
        justify-content: center;
        align-items: center;
        .btn{
          width: 100px;
          margin-left: 10px;
        }
      }
      .content{
        margin: 20px 0;
        .paragraph{
          margin-bottom: 10px;
          display: flex;
          flex-flow: row wrap;
          .block{
            box-sizing: border-box;
            width: 24px;
            height: 24px;
            line-height: 22px;
            font-size: 20px;
            text-align: center;
            margin: 0 5px 5px 0;
          }
          .white{

          }
          .black{
            background-color: #333;
          }
          .green{
            background-color: rgb(28,156,155);
            color: white;
          }
          .out{
            border: 1px solid #ccc;
          }
        }
      }
      .history{
        margin: 20px 0;
        color: white;
        display: flex;
        flex-flow: row wrap;
        .block{
          box-sizing: border-box;
          width: 24px;
          height: 24px;
          line-height: 22px;
          font-size: 20px;
          text-align: center;
          margin: 0 5px 5px 0;
        }
        .green{background-color: rgb(28,156,155);}
        .red{background-color: rgb(244, 182, 120);}
      }

      .out{
        display: flex;
        flex-flow: row nowrap;
        justify-content: center;
        align-items: center;
      }
    }
  </style>
  
  <script>
    const { Configuration, OpenAIApi } = require("openai");
    import {Base64} from "js-base64"

    export default {
      name: "guessBK",
      data(){
        return{
          classes: ["white", "black", "green", "out"],
          punctuationMarks: `,.\\/:;\`~'"()<>!?+-_%`,
          count: 0,
          ans: [],
          his: [],
          input: "",
          isWin: false,
          dialogMode: 0,
          textarea: "",
          isAIThinking: false,
        }
      },
      computed: {
        dialog1(){return this.dialogMode === 1},
        dialog2(){return this.dialogMode === 2}
      },
      mounted(){
        if(this.$route.query.k){
          let code = this.$route.query.k;
          let decode = Base64.decode(decodeURIComponent(code));
          this.generate(decode);
          this.isAIThinking = true;
        } else {
          this.dialogMode = 1;
        }
      },
      methods: {
        generate(article){
          let paragraphs = article.split("##").map(e => e.trim());
          for(let p of paragraphs){
            let temp = [];
            for(let c of p){
              temp.push({
                character: c,
                status: this.punctuationMarks.includes(c) ? 0 : 1 //0 1 2 3
              });
            }
            this.ans.push(temp);
          }
        },
        async ai(){
          const configuration = new Configuration({
            basePath: 'https://api.deepseek.com',
            apiKey: "sk-30653396b0084fa18136b6a2f8da6f2e",
          });
          delete configuration.baseOptions.headers['User-Agent'];
          const openai = new OpenAIApi(configuration);
          this.isAIThinking = true;
          const completion = await openai.createChatCompletion({
            messages: [{ role: "user", content: "" + Date.now() + "23100-200" }],
            model: "deepseek-chat",
          });
          let rawAns = completion.data.choices[0].message.content;
          rawAns = rawAns.replaceAll("**", "").replaceAll("\n\n", "\n").replaceAll("\n", "##");
          this.generate(rawAns);
          this.dialogMode = 0;
        },
        guess(){
          if(this.input.length !== 1) {
            this.$message('');
            return;
          }
          if(this.punctuationMarks.includes(this.input)) {
            this.$message('');
            return;
          }

          //
          if(this.his.some(h => h.character === this.input)){
            this.$message('');
            this.input = "";
            return;
          }

          //
          let flag = false;
          for(let p of this.ans){
            for(let c of p){
              if(c.character === this.input){
                c.status = 2;
                flag = true;
              }
            }
          }

          //
          this.his.push({
            character: this.input,
            status: flag
          });
          this.count++;

          //
          this.input = "";

          //
          this.judgeWin();
        },
        judgeWin(){
          if(this.ans[0].every(c => c.status === 2)){
            for(let p of this.ans){
              for(let c of p){
                if(c.status === 1){
                  c.status = 3;
                }
              }
            }
            this.isWin = true;
            this.$message('');
          }
        },
        customize(){
          let temp = this.textarea;
          temp = temp.replaceAll(/\n/g, "##");
          let fullurl = window.location.href;
          if(fullurl.includes("?")){
            fullurl = fullurl.substring(0, fullurl.indexOf("?"));
          }
          let url = fullurl + "?k=" + encodeURIComponent(Base64.encode(temp));
          navigator.clipboard.writeText(url);
          console.log(url);
          this.$message('');
        }
      }
    }
  </script>
  


